@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Poppins:wght@500;700&display=swap");
.App {
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-image: url("../../public/background.webp");
  background-size: cover;
  font-family: "Poppins";
}

.card {
  display: flex;
  position: relative;
  flex-direction: column;
  width: 400px;
  padding: 32px;
  background-color: white;
  border-radius: 24px;
}

.card-content {
  display: flex;
  position: relative;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}
.card-content #question {
  margin-top: 8px;
  color: #2f527b;
  font-weight: 700;
}
.card-content button {
  margin-top: 8px;
}

.flag {
  display: block;
  width: 48px;
  height: 30px;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

.choice {
  width: 100% !important;
}

#title {
  position: absolute;
  top: -32px;
  left: 0px;
  width: calc(100% + 32px);
  font-size: 24px;
  font-weight: 700;
  color: #F2F2F2;
}

footer {
  display: flex;
  position: fixed;
  bottom: 0;
  width: 464px;
  height: 32px;
  justify-content: center;
  align-content: center;
  text-align: center;
  color: white !important;
  font-family: "Montserrat";
  font-size: 0.8em;
}
footer a {
  color: white !important;
}

.trophy {
  width: 200px;
  height: 140px;
  background-image: url(https://image.freepik.com/free-vector/team-happy-employees-winning-award-celebrating-success-business-people-enjoying-victory-getting-gold-cup-trophy-vector-illustration-reward-prize-champions-s_74855-8601.jpg);
  background-size: 100% 100%;
  align-self: center;
}

#results {
  font-size: 30px;
  font-weight: 700;
  color: #1D355D;
  align-self: center;
}

#caption {
  color: #1D355D;
  text-align: center;
  align-self: center;
}

#score {
  color: #60BF88;
  font-size: 24px;
  font-weight: 700;
  align-self: center;
}

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin-top: -8px;
  margin-left: -8px;
  border-radius: 50%;
  border: 6px solid #2f527b;
  border-color: #2f527b transparent #2f527b transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
img#decoration {
  z-index: 1;
  position: absolute;
  width: 180px;
  height: auto;
  right: -32px;
  top: -80px;
}

@media only screen and (max-width: 576px) {
  .card {
    width: calc(100% - 128px) !important;
  }
}
